import React, { Component } from 'react'

import Joyride, { STATUS } from 'react-joyride'

import { FormattedMessage } from 'react-intl'
import Tooltip from 'components/TourWindow'


export default class TourPartial extends Component {

  constructor(props) {
    super(props)

    /* eslint-disable */
    this.state = {
      run: true,
      steps: [
        {
          content: <FormattedMessage id="partial-tour-2" defaultMessage="  ,  ,    ,    .        ." />,
          target: '.data-tut-have',
        },
        {
          content: <FormattedMessage id="partial-tour-3" defaultMessage="  ,  ,     ,    ." />,
          floaterProps: {
            disableAnimation: true,
          },
          spotlightPadding: 20,
          target: '.data-tut-get',
        },
        {
          content: <FormattedMessage id="partial-tour-4" defaultMessage="        .       ....   ,    " />,
          floaterProps: {
            disableAnimation: true,
          },
          spotlightPadding: 20,
          target: '.data-tut-status',
        },
        {
          content: <FormattedMessage id="partial-tour-5" defaultMessage="  ,          Swap.online    " />,
          floaterProps: {
            disableAnimation: true,
          },
          spotlightPadding: 20,
          target: '.data-tut-togle',
        },
        {
          content: <FormattedMessage
            id="partial-tour-6"
            defaultMessage="  ,    URL  .  ,   ." />,
          floaterProps: {
            disableAnimation: true,
          },
          spotlightPadding: 20,
          target: '.data-tut-Exchange',
        },
        {
          content: <FormattedMessage
            id="partial-tour-7"
            defaultMessage="   ,       .      .         ." />,
          floaterProps: {
            disableAnimation: true,
          },
          spotlightPadding: 20,
          target: '.data-tut-Orderbook',
        },
      ],
    }
    /* eslint-enable */

  }

  handleJoyrideCallback = (data) => {
    const { status } = data
    const finishedStatuses = [STATUS.FINISHED, STATUS.SKIPPED]

    if (finishedStatuses.includes(status)) {
      this.setState({ run: false })
    }
  };

  render() {
    const { run, steps } = this.state
    const { closeTour, isTourOpen } = this.props

    return (
      <div className="demo-wrapper">
        {isTourOpen && <Joyride
          callback={this.handleJoyrideCallback}
          continuous
          run={run}
          scrollToFirstStep
          tooltipComponent={(props) => <Tooltip closeTour={closeTour} {...props} />}
          showProgress
          showSkipButton
          steps={steps}
          styles={{
            options: {
              zIndex: 10000,
              arrowColor: '#302272',
            },
          }}
        />}
      </div>
    );
  }
}



